<template>
  <el-container>
    <el-button @click="getValue">抓值</el-button>
    <el-card class="box-card test" v-for="(item, index) in SMALL" :key="index">
      <div slot="header" class="clearfix">
        <span>{{ item.PROJECT_SMALL_NAME }}</span>
      </div>
      <div
        v-for="(option, optionIndex) in item.SMALL_OPTION"
        :key="optionIndex"
        class="text item"
      >
        <template>
          <el-date-picker
            v-model="
              dynamicDefindes[
                `SMALL_${index}_OPTION_CLASS_${optionIndex}_${option.OPTION_CLASS}`
              ]
            "
            value-format="yyyy-MM-dd"
            type="date"
            placeholder="選擇日期"
            @change="QQ"
          >
          </el-date-picker>
        </template>
        <!-- <template v-else-if="option.OPTION_CLASS === '2'">
          <el-select v-model="value" placeholder="請選擇">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </template> -->
      </div>
    </el-card>
  </el-container>
</template>

<script>
import { testdata } from "../testdata.js";
import axios from "axios";
export default {
  data() {
    return {
      //假設data為父組件給的資料
      data: testdata,
      SMALL: testdata[0].ITEMS[0].SMALL,
      //動態定義
      dynamicDefindes: {},
      //下拉選單假資料
      options: [
        {
          value: "选项1",
          label: "黄金糕"
        },
        {
          value: "选项2",
          label: "双皮奶"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        },
        {
          value: "选项4",
          label: "龙须面"
        },
        {
          value: "选项5",
          label: "北京烤鸭"
        }
      ],
      value: ""
    };
  },
  computed: {},
  methods: {
    defindes() {
      this.SMALL.forEach((item, smallIndex) => {
        item.SMALL_OPTION.forEach((option, optionIndex) => {
          this.$set(
            this.dynamicDefindes,
            `SMALL_${smallIndex}_OPTION_CLASS_${optionIndex}_${option.OPTION_CLASS}`,
            ""
          );
          // this.dynamicDefindes[
          //   `SMALL_${smallIndex}_OPTION_CLASS_${optionIndex}_${item.OPTION_CLASS}`
          // ] = "";
        });
      });
    },
    getValue() {
      console.log(this.dynamicDefindes);
    },
    QQ(value) {
      console.log(value);
    }
  },
  mounted() {
    // console.log(this.data);
    // console.log(this.SMALL);
    this.defindes();
  }
};
</script>
<style>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 400px;
}
.test {
  height: 620px !important;
  margin: 10px 15px !important ;
}
</style>



