<template>
  <el-row :gutter="0" class="full-height footer-center">
    <a @click="drawer = true">
      <el-col :span="6" class="full-height col-center">
        <div class="grid-content">
          <i class="fa fa-list"></i>
          <div>選單</div>
        </div>
      </el-col>
    </a>
    <el-col :span="6" class="full-height col-center">
      <div class="grid-content">
        <i class="fa fa-search"></i>
        <div>搜尋</div>
      </div>
    </el-col>
    <el-col :span="6" class="full-height col-center">
      <div class="grid-content">
        <i class="fa fa-map-marker"></i>
        <div>定位</div>
      </div>
    </el-col>
    <el-col :span="6" class="full-height col-center">
      <div class="grid-content">
        <i class="fa fa-refresh"></i>
        <div>刷新</div>
      </div>
    </el-col>
    <el-drawer
      title="我是标题"
      :visible.sync="drawer"
      :with-header="false"
      :modal="true"
      :modal-append-to-body="false"
    >
      <span>我来啦!</span>
    </el-drawer>
  </el-row>
</template>

<script>
export default {
  data() {
    return {
      drawer: false
    };
  },
  methods: {
    // showDrawer() {
    //   console.log("123");
    //   this.$emit("showDrawer", true);
    // }
  }
};
</script>
<style>
.full-height {
  margin: 0 !important;
  height: 100%;
}
.grid-content {
  color: #f5947e;
  text-align: center;
}
.col-center {
  padding: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-center .col-center:first-child {
  border: none;
}
.footer-center .col-center {
  border-left: 1px solid #ccc;
}
.col-center i {
  font-size: 20px;
}
.col-center > div {
  font-size: 14px;
}
</style>



